<template>
  <div
    @click="checkClick"
    ref="invoiceWrap"
    class="invoice-wrap flex flex-column"
  >
    <form @submit.prevent="submitForm" class="invoice-content">
      <Loading v-show='loading'/>
      <div class="flex new-invoice-heading">
        <h1>New Invoice</h1>
        <div class="close-container" @click="closeInvoice">
          <div class="leftright"></div>
          <div class="rightleft"></div>
          <label class="close">close</label>
        </div>
      </div>

      <!-- Bill From  start -->
      <div class="bill-from flex flex-column">
        <h4>Bill Form</h4>
        <div class="input flex flex-column">
          <label for="billerStreetAddress">Street Address</label>
          <input
            required
            type="text"
            id="billerStreetAddress"
            v-model="billerStreetAddress"
          />
        </div>

        <div class="location-details flex">
          <div class="input flex flex-column">
            <label for="billerCity">City</label>
            <input required type="text" id="billerCity" v-model="billerCity" />
          </div>
          <div class="input flex flex-column">
            <label for="billerZipCode">Zip Code</label>
            <input
              required
              type="text"
              id="billerZipCode"
              v-model="billerZipCode"
            />
          </div>
          <div class="input flex flex-column">
            <label for="billerCountry">Country</label>
            <input
              required
              type="text"
              id="billerCountry"
              v-model="billerCountry"
            />
          </div>
        </div>
      </div>
      <!-- Bill From  End -->

      <!-- Bill to Start -->
      <div class="bill-to flex flex-column">
        <h4>Bill To</h4>
        <div class="input flex flex-column">
          <label for="clientName">Client"s Name</label>
          <input required type="text" id="clientName" v-model="clientName" />
        </div>
        <div class="input flex flex-column">
          <label for="clientEmail">Client's Email</label>
          <input required type="text" id="clientEmail" v-model="clientEmail" />
        </div>
        <div class="input flex flex-column">
          <label for="clientStreetAddress">Street Address</label>
          <input
            required
            type="text"
            id="clientStreetAddress"
            v-model="clientStreetAddress"
          />
        </div>

        <div class="location-details flex">
          <div class="input flex flex-column">
            <label for="clientCity">City</label>
            <input required type="text" id="clientCity" v-model="clientCity" />
          </div>
          <div class="input flex flex-column">
            <label for="clientZipCode">Zip Code</label>
            <input
              required
              type="text"
              id="clientZipCode"
              v-model="clientZipCode"
            />
          </div>
          <div class="input flex flex-column">
            <label for="clientCountry">Country</label>
            <input
              required
              type="text"
              id="clientCountry"
              v-model="clientCountry"
            />
          </div>
        </div>
      </div>
      <!-- Bill to End -->

      <!-- Invoice Work Details Start -->
      <div class="invoice-work flex flex-column">
        <div class="payment flex">
          <div class="input flex flex-column">
            <label for="invoiceDate">Invoice Date</label>
            <input
              disabled
              type="text"
              id="invoiceDate"
              v-model="invoiceDate"
            />
          </div>
          <div class="input flex flex-column">
            <label for="paymentDueDateUnix">Payment Due</label>
            <input
              disabled
              type="text"
              id="paymentDueDateUnix"
              v-model="paymentDueDateUnix"
            />
          </div>
        </div>
        <div class="input flex flex-column">
          <label for="paymentTerms">Payment Terms</label>
          <select required id="paymentTerms" v-model="paymentTerms">
            <option value="30">Net 30 Days</option>
            <option value="60">Net 60 Days</option>
          </select>
        </div>
        <div class="input flex flex-column">
          <label for="productDescription">Payment Description</label>
          <input
            disabled
            type="text"
            id="productDescription"
            v-model="productDescription"
          />
          <!-- work Item -->
          <div class="work-items">
            <h3>item List</h3>
            <table class="table-list">
              <tr class="table-heading flex">
                <th class="item-name">Item Name</th>
                <th class="qty">Qty</th>
                <th class="price">Price</th>
                <th class="totle">Totle</th>
              </tr>
              <tr
                class="table-items flex"
                v-for="(item, index) in invoiceItemList"
                :key="index"
              >
                <td class="item-name">
                  <input type="text" v-model="item.itemName" />
                </td>
                <td class="qty"><input type="text" v-model="item.qty" /></td>
                <td class="price">
                  <input type="text" v-model="item.price" />
                </td>
                <td class="totle flex">
                  ${{ (item.totle = item.qty * item.price) }}
                </td>
                <img
                  @click="deleteInvoiceItem(item, id)"
                  src="@/assets/icon-delete.svg"
                  alt=""
                />
              </tr>
            </table>

            <div @click="addNewInvoiceItem" class="flex button">
              <img src="@/assets/icon-plus.svg" alt="" />
              Add New Item
            </div>
          </div>
        </div>
      </div>
      <!-- Invoice Work Details End  -->

      <!-- Save/Exit  start-->
      <div class="save flex">
        <div class="left">
          <button @click="closeInvoice" type="button" class="red">
            Cancle
          </button>
        </div>
        <div class="right flex">
          <button @click="saveDraft" type="submit" class="dark-purple">Save Draft</button>
          <button @click="publishInvoice" type="submit" class="purple">Create Invoice</button>
        </div>
      </div>
      <!-- Save/Exit End -->
    </form>
  </div>
</template>

<script>
import db from '../firebase/firebaseinit'
import Loading from "../components/loading.vue"
import { mapMutations } from "vuex";
import { uid } from "uid";
export default {
  name: "InvoiceModal",
  components: {
    Loading
  },
  data() {
    return {
       dateOptions: { year: "numeric", month: "short", day: "numeric" },
      loading:null,
      billerStreetAddress: null,
      billerCity: null,
      billerZipCode: null,
      billerCountry: null,
      clientName: null,
      clientEmail: null,
      clientStreetAddress: null,
      clientCity: null,
      clientZipCode: null,
      clientCountry: null,
      invoiceDateUnix: null,
      invoiceDate: null,
      paymentTerms: null,
      paymentDueDateUnix: null,
      paymentDueDate: null,
      productDescription: null,
      invoicePending: null,
      invoiceDraft: null,
      invoiceItemList: [],
      invoiceTotal: 0,
    };
  },

  created() {
    // get current date for invoice date field
    if (!this.editInvoice) {
      this.invoiceDateUnix = Date.now();
      this.invoiceDate = new Date(this.invoiceDateUnix).toLocaleDateString(
        "en-us",
        this.dateOptions
      );
    }
  },

  methods: {
     ...mapMutations(["TOGGLE_INVOICE", "TOGGLE_MODAL"]),
      //  ...mapMutations(["TOGGLE_INVOICE", "TOGGLE_MODAL", "TOGGLE_EDIT_INVOICE"]),
    // ...mapActions(["UPDATE_INVOICE", "GET_INVOICES"]),
    checkClick(e) {
      if (e.target === this.$refs.invoiceWrap) {
        this.TOGGLE_MODAL();
      }
    },
    closeInvoice() {
      this.TOGGLE_INVOICE();
    },

    addNewInvoiceItem() {
      this.invoiceItemList.push({
        id: uid(),
        itemName: "",
        qty: "",
        price: 0,
        total: 0,
      });
    },

    deleteInvoiceItem(Item) {
      const Id = Item.id;
      this.invoiceItemList = this.invoiceItemList.filter(
        (item) => item.id !== Id
      );
    },

    calInvoiceItemTotal(){
      this.invoiceTotal=0;
      this.invoiceItemList.forEach((item)=>{
        this.invoiceTotal+=item.total
      })
    },

    publishInvoice(){
      this.invoicePending=true;
    },

    saveDraft(){
      this.invoiceDraft=true;
    },

    async uploadInvoice(){
      if(this.invoiceItemList.length<=0){
        alert("Please ensure you filled out work items !!!");
        return;
      }

      this.loading=true; 

      this.calInvoiceItemTotal();

      const dataBase=db.collection('invoices').doc();

      await dataBase.set({
        invoiceId: uid(6),
        billerStreetAddress: this.billerStreetAddress,
        billerCity: this.billerCity,
        billerZipCode: this.billerZipCode,
        billerCountry: this.billerCountry,
        clientName: this.clientName,
        clientEmail: this.clientEmail,
        clientStreetAddress: this.clientStreetAddress,
        clientCity: this.clientCity,
        clientZipCode: this.clientZipCode,
        clientCountry: this.clientCountry,
        invoiceDate: this.invoiceDate,
        invoiceDateUnix: this.invoiceDateUnix,
        paymentTerms: this.paymentTerms,
        paymentDueDate: this.paymentDueDate,
        paymentDueDateUnix: this.paymentDueDateUnix,
        productDescription: this.productDescription,
        invoiceItemList: this.invoiceItemList,
        invoiceTotal: this.invoiceTotal,
        invoicePending: this.invoicePending,
        invoiceDraft: this.invoiceDraft,
        invoicePaid: null,
      })
      
      this.loading=false;

     this.TOGGLE_INVOICE();
    },

    submitForm(){
      this.uploadInvoice();
    }
  },

  watch: {
    paymentTerms() {
      const futureDate = new Date();
      this.paymentDueDateUnix = futureDate.setDate(
        futureDate.getDate() + parseInt(this.paymentTerms)
      );
      this.paymentDueDate = new Date(
        this.paymentDueDateUnix
      ).toLocaleDateString("en-us", this.dateOptions);
    },
  },
};
</script>

<style lang="scss">
// COLORS
$softorange: #ff8f00;
$tomatored: #ec5757;
$mediumblu: #1e272d;

.new-invoice-heading {
  flex-direction: row;
  justify-content: space-between;
}
.close-container {
  position: relative;
  // margin: auto;
  width: 50px;
  height: 50px;
  // margin-top: 100px;
  cursor: pointer;
}

.leftright {
  height: 4px;
  width: 50px;
  position: absolute;
  margin-top: 24px;
  background-color: $softorange;
  border-radius: 2px;
  transform: rotate(45deg);
  transition: all 0.3s ease-in;
}

.rightleft {
  height: 4px;
  width: 50px;
  position: absolute;
  margin-top: 24px;
  background-color: $softorange;
  border-radius: 2px;
  transform: rotate(-45deg);
  transition: all 0.3s ease-in;
}

.close {
  margin: 60px 0 0 5px;
  position: absolute;
  color: white;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 0.6em;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: all 0.3s ease-in;
  opacity: 0;
}

.close-container:hover .leftright {
  transform: rotate(-45deg);
  background-color: $tomatored;
}
.close-container:hover .rightleft {
  transform: rotate(45deg);
  background-color: $tomatored;
}
.close-container:hover label {
  opacity: 1;
}

.invoice-wrap {
  position: fixed;
  top: 0;
  left: 0;

  // background: rgba(255,255,255,0.5);

  // backdrop-filter: blur(5px);
  background-color: transparent;
  z-index: 1;
  width: 100%;
  height: 100vh;
  overflow: scroll;
  &::-webkit-scrollbar {
    display: none;
  }
  @media (min-width: 900px) {
    left: 90px;
   
  }

  .invoice-content {
    position: relative;
    padding: 56px;
    max-width: 700px;
    width: 100%;

    backdrop-filter: blur(7px);
    background: #141625c9;
    color: #fff;
    box-shadow: 17px 6px 40px 5px #11121b;
    h1 {
      margin-bottom: 48px;
      color: #fff;
    }
    h3 {
      margin: 18px 0;

      font-size: 18px;
      color: #777f98;
    }
    h4 {
      color: #7c5dfa;
      font-size: 12px;
    }

    // Bill to / Bill Forma
    .bill-to,
    .bill-from {
      margin-bottom: 48px;

      .location-details {
        gap: 16px;

        div {
          flex: 1;
        }
      }
    }

    // invoice work

    .invoice-work,
    .payment {
      gap: 24px;
      div {
        flex: 1;
      }
    }

    .work-item,
    .table-list {
      width: 100%;

      // item tabel styling

      .table-heading,
      .table-items {
        gap: 16px;
        font-size: 12px;

        .item-name {
          flex-basis: 50%;
        }

        .qty {
          flex-basis: 10%;
        }

        .price {
          flex-basis: 20%;
        }

        .total {
          flex-basis: 20%;
          align-self: center;
        }
      }

      .table-heading {
        margin-bottom: 16px;
        th {
          text-align: left;
        }
      }

      .table-items {
        position: relative;
        margin-bottom: 24px;

        img {
          position: absolute;
          top: 15px;
          right: 0;
          width: 12px;
          height: 16px;
        }
      }
    }

    .button {
      color: rgb(255, 255, 255);
      background-color: #252945;
      align-items: center;
      justify-content: center;
      width: 100%;

      img {
        margin-right: 4px;
      }
    }

    .save {
      margin-top: 60px;

      div {
        flex: 1;
      }

      .right {
        justify-content: flex-end;
      }
    }
  }

  .input {
    margin-bottom: 24px;
  }

  label {
    font-size: 12px;
    margin-bottom: 5px;
  }

  input,
  select {
    width: 100%;
    // background-color:#1e2139 ;
    background: #ffffff83;

    backdrop-filter: blur(10px);
    background: #1e2139;
    color: #fff;
    border-radius: 4px;
    padding: 12px 4px;
    border: none;

    &:focus {
      outline: none;
    }
  }
}
</style>
